.App {
  text-align: center;
  font-family: 'Reem Kufi', sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.barber-logo {
  width: 20%;
}

.group-image {
  border-radius: 10px;
  width: 60%;
}

.image-hours {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-evenly;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}

.image-gallery {
  width: 40%;
}

