@media only screen and (max-width: 480px) {
  .barber-logo {
    width: 70%;
  }

  .group-image {
    width: 90%;
  }

  .image-hours {
    flex-direction: column-reverse;
  }

  .image-gallery {
    width: 100%;
  }
}